import React, { useState } from "react";
import * as Yup from "yup";
import { Formik, Form, Field } from "formik";
// import { toast } from "react-toastify";

import { makeStyles } from "@material-ui/core/styles";
import { green } from "@material-ui/core/colors";

import Box from "@material-ui/core/Box";
import InputLabel from "@material-ui/core/InputLabel";
import MenuItem from "@material-ui/core/MenuItem";
import FormControl from "@material-ui/core/FormControl";
import Select from "@material-ui/core/Select";
import {
  //   Dialog,
  DialogContent,
  Button,
  DialogActions,
  // CircularProgress,
  TextField,
  // Switch,
  // FormControlLabel,
} from "@material-ui/core";

import Container from "@material-ui/core/Container";
// import toastError from "../../errors/toastError";
import axios from "axios";

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    flexWrap: "wrap",
  },
  container: {
    paddingTop: theme.spacing(4),
    paddingBottom: theme.spacing(4),
  },

  multFieldLine: {
    display: "flex",
    "& > *:not(:last-child)": {
      marginRight: theme.spacing(1),
    },
    justifyContent: "space-between",
  },
  btnWrapper: {
    position: "relative",
  },
  ok: {
    color: green[500],
  },
  err: {
    color: "red",
  },
}));

const isValidURL = (string) => {
  let resURL = string.match(
    /(http(s)?:\/\/.)?(www\.)?[-a-zA-Z0-9@:%._\+~#=]{2,256}\.[a-z]{2,6}\b([-a-zA-Z0-9@:%_\+.~#?&//=]*)/g
  );
  return resURL !== null;
};

const SessionSchema = Yup.object().shape({
  URL: Yup.string()
    .min(10, "Too Short!")
    .max(50, "Too Long!")
    .required("Required"),
  platfrom: Yup.string().required("Required"),
});

const ClientAPI = () => {
  const [platfromValue, setplatfromValue] = useState("");
  //   const [URLValue, setURLValue] = useState("");
  const [Valid, setValid] = useState("");
  const [copyCase, setCopyCase] = useState(false);

  const handleChange = (event) => {
    setplatfromValue(event.target.value);
  };

  const handleURLValueChange = (event) => {
    // setURLValue(event.target.value);

    if (isValidURL(event.target.value) === true) {
      // setValid("correct formula");
      axios
        .get(event.target.value)
        .then((res) => {
          if (res.status === 200) {
            setValid("valid url");
          } else {
            setValid("Invalid url");
          }
        })

        .catch((err) => {
          setValid("incorrect formula");
          // console.log(err)
        });
    } else {
      setValid("format error");
    }
  };
  const handleSaveClintAPI = () => {
    if (Valid === "valid url") {
      console.log("save");
      // axios.post(`https://jsonplaceholder.typicode.com/users`, {user})
      // .then(res=> {
      //     console.log(res)
      // })
    }
  };

  const handelCopy = () => {
    navigator.clipboard.writeText(initialValuesClintAPI.GenerateToken);
    setCopyCase(true);
    setTimeout(() => {
      setCopyCase(false);
    }, 1500);
  };

  const classes = useStyles();

  const initialValuesClintAPI = {
    GenerateToken: "!@#$%^&*",
    platfrom: platfromValue,
    URL: "",
  };

  return (
    <Container maxWidth="lg" className={classes.container}>
      <Formik
        initialValues={initialValuesClintAPI}
        enableReinitialize={true}
        validationSchema={SessionSchema}
        onSubmit={(values, actions) => {
          // setTimeout(() => {
          // console.log(values)
          handleSaveClintAPI();
          // actions.setSubmitting(false);
          // }, 400);
        }}
      >
        {({ values, touched, errors, isSubmitting }) => (
          <Form>
            <DialogContent dividers>
              <div className={classes.multFieldLine}>
                <h3 style={{ margin: "0px " }}>Generate Token</h3>
                <Button
                  // type=""
                  color={copyCase === false ? "primary" : "danger"}
                  // disabled={isSubmitting}
                  variant="contained"
                  className={classes.btnWrapper}
                  onClick={handelCopy}
                >
                  {copyCase === false ? "copy" : "copied"}
                </Button>
              </div>
              <Box sx={{ minWidth: 120 }} style={{ margin: "25px 0" }}>
                <FormControl fullWidth variant="outlined" margin="dense">
                  <InputLabel id="demo-simple-select-label">
                    Platfrom
                  </InputLabel>
                  <Select
                    labelId="demo-simple-select-label"
                    id="demo-simple-select"
                    name="platfrom"
                    // value={platfrom}
                    label="Platfrom"
                    onChange={handleChange}
                    error={touched.platfrom && Boolean(errors.platfrom)}
                    helperText={touched.platfrom && errors.platfrom}
                  >
                    <MenuItem value="Woocommerce">Woocommerce</MenuItem>
                    <MenuItem value="Shopify">Shopify</MenuItem>
                    <MenuItem value="Salla">Salla</MenuItem>
                    <MenuItem value="Custom">Custom</MenuItem>
                  </Select>
                </FormControl>
              </Box>
              <div>
                <Field
                  as={TextField}
                  label="URL"
                  type="text"
                  // autoFocus
                  placeholder="example: https://www.Domain Name.com"
                  rows={4}
                  fullWidth
                  name="URL"
                  error={touched.URL && Boolean(errors.URL)}
                  helperText={touched.URL && errors.URL}
                  variant="outlined"
                  margin="dense"
                  onKeyUp={handleURLValueChange}
                />
              </div>

              {Valid === "valid url" ? (
                <div className={classes.ok}>{Valid}</div>
              ) : (
                <div className={classes.err}>{Valid}</div>
              )}
            </DialogContent>
            <DialogActions>
              <Button
                type="submit"
                color="primary"
                // disabled={isSubmitting}
                variant="contained"
                className={classes.btnWrapper}
              >
                save
              </Button>
            </DialogActions>
          </Form>
        )}
      </Formik>
    </Container>
  );
};

export default ClientAPI;
